import * as util from '../api/common'
import { required, minLength, between } from 'vuelidate/lib/validators'

export default {
  name: 'Login',
  data () {
    return {
      // note: changing this line won't causes changes
      // with hot-reload because the reloaded component
      // preserves its current state and we are modifying
      // its initial state.
      mobile: '17696041210',
      password: '123456',
      token: '',
      show2: false,
      userType: ''// util.getQueryParam('userType')
    }
  },
  validations: {
    mobile: {
      required,
      minLength: minLength(4)
    }
  },
  created() {
    function getQueryString(name) {
      var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
      var r = window.location.search.substr(1).match(reg);
      if (r != null) return unescape(r[2]); return null;
    }
    this.userType=getQueryString("userType")
    console.log("!",this.userType)
  },
  methods: {
    registered () {
      console.log('去注册流程')
      var self = this
      self.$router.push('/registered')
    },
    login () {
      var self = this

      var urlLogin = ''

      //alert('userType的值:' + self.userType)

      if (self.userType === 'shopeeker') {
        urlLogin = util.baseURL + 'shopeeker/login' // 供应商
        localStorage.setItem('userType', 'shopeeker')
      } else {
        urlLogin = util.baseURL + 'buyer/login' // 采购
        localStorage.setItem('userType', 'buyer')
      }
      let params = new URLSearchParams()
      params.append('mobile', self.mobile)
      params.append('password', self.password)

      if (self.mobile === '' || self.password === '') {
        return
      }
      self.$axios.post(urlLogin, params).then(res => {
        console.log(res.data)
        if (res.data.success) {
          self.token = res.data.success.token
          localStorage.setItem('token', self.token)
          self.$router.push('/userCenter')
        } else {
          this.$vux.toast.text(res.data.message)
        }
      })
    },
    status (validation) {
      return {
        error: validation.$error,
        dirty: validation.$dirty
      }
    }
  }
}



